@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html, body {
  height: 100%;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7; }

h1 {
  font-family: 'Anton', sans-serif; }

a.link-unstyled {
  color: inherit; }

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none; }

.btn {
  padding: 0.375rem 2rem; }

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40; }

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40; }

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40; }

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5); }

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040; }

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040; }

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040; }

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5); }

.Navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: #1C3643; }
  .Navbar__brand {
    color: #ffffff;
    display: flex;
    align-items: center; }
  .Navbar__brand:hover {
    color: #ffffff;
    text-decoration: none; }
  .Navbar__brand-logo {
    margin-right: 0.5rem; }

.home {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: url(/curso_de_react.js/static/media/stars.1d17360a.svg), #1B1B25;
  justify-content: center;
  align-items: center; }
  .home__message {
    margin: 0 10px;
    color: white;
    max-width: 300px; }
    .home__message--logo {
      max-width: 200px;
      width: 100%; }
  .home__img {
    max-width: 400px;
    width: 100%; }

@media screen and (min-width: 768px) {
  .home {
    flex-direction: row; } }

.BadgeNew__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/curso_de_react.js/static/media/stars.1d17360a.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff; }

.Badge {
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: 100%;
  max-height: 380px;
  color: black; }
  .Badge__header {
    padding: 0.5rem 0;
    height: 80px;
    background: #1b1b25;
    display: flex;
    justify-content: center; }
  .Badge__section-name {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0; }
    .Badge__section-name--avatar {
      border-radius: 50%;
      margin-right: 1rem;
      width: 120px;
      height: 120px; }
  .Badge__section-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 0;
    background: #f4f4f7; }
  .Badge__footer {
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #98ca3f;
    font-weight: bold;
    font-style: italic; }

.List-element {
  display: flex;
  background-color: white;
  align-items: center;
  box-shadow: 2px 4px 11px -1px rgba(0, 0, 0, 0.75);
  margin: 2em auto;
  padding: 1em 1.5em;
  border-radius: 10px; }
  .List-element__avatar {
    width: 7em;
    height: 7em;
    border-radius: 40px;
    margin-right: 2em;
    box-shadow: 0px 3px 8px -1px rgba(0, 0, 0, 0.75); }
  .List-element__info__twitter {
    color: #00a4d5;
    font-weight: 400; }
    .List-element__info__twitter--logo {
      width: 1.5em;
      height: 1.5em;
      margin-right: .5em; }

@media only screen and (max-width: 490px) {
  .List-element {
    flex-direction: column;
    text-align: center; }
    .List-element__avatar {
      margin: 1em auto; } }

.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/curso_de_react.js/static/media/stars.1d17360a.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem; }

.Badges__hero > .Badges__container {
  display: flex;
  flex-direction: column;
  align-items: center; }

.Badges__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem; }

.Badges_conf-logo {
  margin-bottom: 2rem; }

.Badges__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem; }

